<template>
  <div class="mainboard">
    <!-- 네브바 -->
    <nav-header></nav-header>
    <div id="board_body">

      <!-- select box , 검색-->
      <v-container id="search" fluid>
        <v-row align="center">
          <v-col class="d-flex" cols="3" sm="3">
            <v-select
              :items="items1"
              :value="items1[0]"
              v-model="value"
            ></v-select>
          </v-col>
          <v-col cols="7">
            <v-text-field v-model="search" clearable flat solo-inverted hide-details label="Search" @keyup.enter="searchdata"></v-text-field>
          </v-col>
          <v-btn icon @click="searchdata"><v-icon>mdi-magnify</v-icon></v-btn>
        </v-row>
      </v-container>

      <b-tabs content-class="mt-3">
        <!-- 게시판 탭 -->
        <b-tab title="게시판" @click="tab_board" active> 
          <div>
            <h5 style="text-align:left; margin:10px 0px 10px 20px;">이 달의 게시글</h5>
            <!-- 이 달의 블로그 -->
            <div>
              <v-row id="reco">
                <v-hover v-slot:default="{ hover }" v-for="(board,index) in bestboards" :key="board.articleNo">
                  <v-card class="auto" color="grey lighten-4" max-width="250" max-height="350" @click="toArticle(board)">
                    <v-img max-width="300" height="250" :src="bestuserImage[index]" img-alt="Image" img-top>
                      <v-expand-transition>
                        <div
                          v-if="hover"
                          class="d-flex transition-fast-in-fast-out blue-grey darken-2 v-card--reveal display-3 white--text"
                          style="height: 100%;"
                        >
                          <div class="font-weight-light-6 title mb-2">
                            <v-avatar><img :src="bestuserImage[index]" style="width:50px; height:50px"></v-avatar>
                            <br>{{board.nickName}}
                            <br><br><p>{{board.title}}</p>
                          </div>
                        </div>
                      </v-expand-transition>
                    </v-img>
                  </v-card>
                </v-hover>
              </v-row>
            </div>
            <hr>

            <!-- 게시판 하단 메뉴 -->
            <b-tabs>
              <b-tab title="알고싶어요!" @click="tab_qna">

              </b-tab>
          
              <b-tab title="게시글" @click="tab_boardlist">
              </b-tab>
            </b-tabs>
            <br>
            <!-- <div class="left">
              <b-tabs content-class="mt-3">
                <v-row id="board_title">       
                  <v-col id="left" cols="8">
                    <li><v-tab>알고싶어요!!</v-tab></li>
                    <li><v-tab>게시글</v-tab></li>
                  </v-col>
                  <v-col id="right" cols="3" sm="3">
                    <v-select
                    :items="items2"
                    label="분류"
                    ></v-select>
                  </v-col>
                </v-row>
              </b-tabs>
            </div> -->

            <!-- 게시판 하단 -->
            <div>
              <b-card :img-src="userImage[index]" img-left class="width:150px;height:150px;" v-for="(board,index) in boards" :key="board.articleNo">
                <b-card-title @click="toArticle(board)">
                  <button>{{board.title}}</button>
                </b-card-title>
                <b-card-text>
                  <div id="card_content">
                    <!-- <div
                      style="width:1050px; 
                        display:-webkit-box;
                        -webkit-line-clamp:3;
                        -webkit-box-orient:vertical;
                        line-height:20px;
                        height:37px;
                        justify-content:space-between; 
                        overflow:hidden; 
                        white-space:normal;
                        text-overflow:ellipsis;
                        word-break:break-all;
                        table-layout:fixed;"
                    > -->
                    <button><v-html v-html=board.content></v-html></button>
                  </div>
                </b-card-text>
                <v-divider></v-divider>
                <b-card-text>
                  <img :src="userImage[index]" style="width:40px;height:40px">{{board.nickName}}({{board.userId}})
                </b-card-text>
              </b-card>
            </div>

            <!-- 페이지네이션 -->
            <v-col>
              <v-container id="page" class="max-width" @click="clickpage">
                <v-pagination
                  v-model="page"
                  :circle="circle"
                  :disabled="disabled"
                  :length="pagelength"
                  :next-icon="nextIcon"
                  :prev-icon="prevIcon"
                  :page="page"
                  :total-visible="totalVisible"
                ></v-pagination>
              </v-container>
            </v-col>
          </div>
        </b-tab>

        <!-- 나머지 탭 -->
        <b-tab title="작품" @click="tab_book"> 
        </b-tab>
        <b-tab title="블로그" @click="tab_blog"> 
        </b-tab>
        <b-tab title="유저" @click="tab_user">
        </b-tab>
      </b-tabs>
    </div>
    
    <!-- 푸터 -->
    <nav-footer></nav-footer>
  </div>
</template>

<script>
import NavHeader from "@/views/Main/NavHeader.vue";
import NavFooter from "@/views/Main/NavFooter.vue";
import axios from 'axios';
export default {
  name: "Main", 
    data() {
      return{
        items1: ['게시판', '작품', '블로그', '유저'],
        items2: ['전체선택', '알고리즘', '웹'],
        selected1: '1',
        selected2: '1',
        rows: 100,
        totalVisible: 10,
        value : "게시판",
        boards : [],
        bestboards : [],
        page: this.$route.params.page,
        search : this.$route.params.searchValue,
        pagelength : 1,
        bestuserImage : [],
        userImage : [],
        // search:this.$route.params.searchValue,
        // page: this.$route.params.page,
      }
    },
  components : {
    NavHeader,
    NavFooter
  },
  computed: { 
    serverURL(){
      return this.$store.getters.serverURL;
    }
  },
  created() {
    this.getboards()
  },
  methods : {
      tab_board() {
        this.$router.push({ name: 'Mainboard', params: { searchValue: this.$route.params.searchValue, page:1 ,key: this.search+this.page }});
      },
      tab_book() {
        this.$router.push({ name: 'Mainbook', params: { searchValue: this.$route.params.searchValue, page:1 ,key: this.search+this.page }});
      },
       tab_blog() {
        this.$router.push({ name: 'Mainblog', params: { searchValue: this.$route.params.searchValue, page:1 ,key: this.search+this.page }});
      },
      tab_user() {
        this.$router.push({ name: 'Mainuser', params: { searchValue: this.$route.params.searchValue, page:1 ,key: this.search+this.page }});
      },
      toArticle(data){
        this.$router.push('/bloghome/' + data.userId + '/articledetail/' + data.articleNo)
      },
      searchdata(){
        if(this.serach == "" || !this.search){
          alert("검색어를 입력하세요.");
          return;
        }
        if(this.value==="게시판"){
          this.$router.push({ name: 'Mainboard', params: { searchValue: this.search, page:1 ,key: this.search+this.page }});
        }else if(this.value==="작품"){
          this.$router.push({ name: 'Mainbook', params: { searchValue: this.search, page:1 ,key: this.search+this.page }});
        }else if(this.value==="블로그"){
          this.$router.push({ name: 'Mainblog', params: { searchValue: this.search, page:1 ,key: this.search+this.page }});
        }else if(this.value==="유저"){
          this.$router.push({ name: 'Mainuser', params: { searchValue: this.search, page:1 ,key: this.search+this.page }});
        }
      },
      getboards(){
        axios.get('/article/articles')
            .then((Response)=>{
              console.log(Response);
              console.log("드러옴");
              if(Response.data.length>4){
                for(let i = 0; i < 5; i++){
                  this.bestboards.splice(i,1,Response.data[i].article);
                  this.bestuserImage.splice(i,1,this.serverURL + Response.data[i].article.profileImg);
                }
              }else{
                for(let i = 0; i < Response.data.length; i++){
                  this.bestboards.splice(i,1,Response.data[i].article);
                  this.bestuserImage.splice(i,1,this.serverURL + Response.data[i].article.profileImg);
                }
              }
          }),

        axios.get('/article/articlescount?title='+ this.$route.params.searchValue)
          .then((Res)=>{
          this.pagelength = parseInt(Math.ceil(Res.data/5));
        }),

        axios.get('/article/articles?searchValue='+this.$route.params.searchValue + '&limitStart=' + ((this.$route.params.page-1)*5) + '&limitEnd=5')
        .then((Response)=>{
          console.log(Response);
          console.log("드러옴");
          for(let i = 0; i < Response.data.length; i++){
              this.boards.splice(i,1,Response.data[i].article);
              this.userImage.splice(i,1,this.serverURL + Response.data[i].article.profileImg);
          }
        })
      },
      tab_boardlist(){
        alert("탭");
      },
      tab_qna(){
        alert("탭");
      },
      clickpage() {
        this.$router.push({ name: 'Mainboard', params: { searchValue: this.$route.params.searchValue, page:this.page ,key: this.$route.params.searchValue+this.page }});
      },
      removeIframe(){
        setTimeout(() => {
          if(document.getElementsByTagName('iframe')){
            let iframes = document.getElementsByTagName('iframe');
            let len = document.getElementsByTagName('iframe').length;
            for(let i = 0; i < len; i++){
              console.log(i);
              iframes[0].parentElement.removeChild(iframes[0]);
            } 
          }else{
            alert('dd')
            setTimeout(this.removeIframe, 100);
          }
        }, 100)
      },
      removeCode(){
        setTimeout(() => {
          if(document.getElementsByTagName('code')){
            let codes = document.getElementsByTagName('code');
            let len = document.getElementsByTagName('code').length;
            for(let i = 0; i < len; i++){
              console.log(i);
              codes[0].parentElement.parentElement.removeChild(codes[0].parentElement);
            } 
          }else{
            alert('dd')
            setTimeout(this.removeCode, 100);
          }
        }, 100)
      }
  },
  updated(){
      window.Prism.highlightAll();
      this.removeIframe();
      this.removeCode();
    }

};
</script>

<style scoped>
#board_body {
  width: 85%;
  margin: 0 100px;
}
#search {
  margin: 0 auto;
}
#card {
  padding: 0 20px;
}
p {
  margin-bottom: 0px;
}
hr {
  margin: 16px 20px;
  padding: 0 20px;
  border: 0;
  height: 2px;
  background: #ccc;
}
#btn_select {
  display: inline-block;
}
#btn {
  float: left;
  margin: 10px;
}
#select {
  float: right;
  padding: 0;
}
.cols {
  padding: 0 0px;
}
#tab {
  display: inline;
}
#left {
  padding: 0 0px;
  width: 50%;
  float: left;
  box-sizing: border-box;
}
#right {
  padding: 0 0px;
  width: 50%;
  float: right;
  box-sizing: border-box;
}
#board_title {
  margin: 0 0px;
}
#page {
  text-align: center;
  margin: 0 auto;
  width: 100%;
}
.v-card--reveal {
  align-items: center;
  bottom: 0;
  justify-content: center;
  opacity: .8;
  position: absolute;
  width: 100%;
}
#reco {
  margin: 0 20px;
}
.card-img-left {
  height: 150px;
}
#card_detail {
  height: 150px;
}
#card_content {
  width:1050px; 
  display:-webkit-box;
  -webkit-line-clamp:3;
  -webkit-box-orient:vertical;
  line-height:20px;
  height:37px;
  justify-content:space-between; 
  overflow:hidden; 
  white-space:normal;
  text-overflow:ellipsis;
  word-break:break-all;
  table-layout:fixed;
}
</style>