<template>
  <div class="bookmark container col order">
    <div class="row">
      <!-- data가 들어갈 부분 -->
      <div class="col" id="main">
        <v-card :elevation="0" class="col-11" id="bookmark_card">
          <v-container fluid>
            <v-row align="center">
              <!-- <v-col class="d-flex" cols="2" sm="2">
                <v-select
                  :items="items1"
                  label="검색"
                ></v-select>
              </v-col> -->
              <v-col md="4" class="ml-auto">
                <v-text-field v-model="search" clearable flat solo-inverted hide-details label="Search"></v-text-field>
              </v-col>
              <v-btn icon ><v-icon>mdi-magnify</v-icon></v-btn>
            </v-row>
          </v-container>

          <!-- 탭 -->
          <b-tabs content-class="mt-3">
            <!-- 게시판 (복붙이라구 수정해야한다구!)--> 
            <b-tab title="게시판" active>
              <v-simple-table>
                <template v-slot:default>
                  <colgroup>
                    <col width="3%">
                    <col width="52%">
                    <col width="30%">
                    <col width="15%">
                  </colgroup>
                  <thead>
                    <th></th>
                    <th>게시글 제목</th>
                    <th>블로그</th>
                    <th>닉네임</th>
                  </thead>
                  <tbody>
                    <tr v-for="articleFav of articleFavList" :key="articleFav.favoriteNo">
                      <td><v-checkbox v-model="check" class="mx-2"></v-checkbox></td>
                      <td class="text-left">{{articleFav.articleTitle}}</td>
                      <td class="text-left">{{articleFav.blogName}}</td>
                      <td class="text-left">{{articleFav.nickName}}</td>
                    </tr>
                  </tbody>
                  <tfoot>
                    <tr>
                      <td><v-checkbox v-model="check" class="mx-2"></v-checkbox></td>
                      <td class="text-left">전체선택</td>
                      <td></td>
                      <td></td>
                      <td><v-btn outlined>삭제</v-btn></td>
                    </tr>
                  </tfoot>
                </template>
              </v-simple-table>
            </b-tab>
            

            <!-- 작품 -->
            <b-tab title="작품">
              <v-simple-table>
                <template v-slot:default>
                  <colgroup>
                    <col width="3%">
                    <col width="52%">
                    <col width="30%">
                    <col width="15%">
                  </colgroup>
                  <thead>
                    <th></th>
                    <th>게시글 제목</th>
                    <th>블로그</th>
                    <th>닉네임</th>
                  </thead>
                  <tbody>
                    <tr v-for="articleFav of articleFavList" :key="articleFav.favoriteNo">
                      <td><v-checkbox v-model="check" class="mx-2"></v-checkbox></td>
                      <td class="text-left">{{articleFav.articleTitle}}</td>
                      <td class="text-left">{{articleFav.blogName}}</td>
                      <td class="text-left">{{articleFav.nickName}}</td>
                    </tr>
                  </tbody>
                  <tfoot>
                    <tr>
                      <td><v-checkbox v-model="check" class="mx-2"></v-checkbox></td>
                      <td class="text-left">전체선택</td>
                      <td></td>
                      <td></td>
                      <td><v-btn outlined>삭제</v-btn></td>
                    </tr>
                  </tfoot>
                </template>
              </v-simple-table>
            </b-tab>
          </b-tabs>

          <!-- 페이지네이션 -->
          <v-col cols="12">
            <v-container id="page" class="max-width">
              <v-pagination
                v-model="page"
                :circle="circle"
                :disabled="disabled"
                :length="10"
                :next-icon="nextIcon"
                :prev-icon="prevIcon"
                :page="page"
                :total-visible="totalVisible"
              ></v-pagination>
            </v-container>
          </v-col>
        </v-card>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: "bookmark",
  components: {},
  data() {
    return {
      items1: ['전체선택', '알고리즘', '웹'],
      articleFavList:'',
      selected : '1',
      page: 1,
      // pagination 이동하게 만들어야함
      rows: 100
    };
  },
  created(){
    axios.get(`/article/articlefavorites`)
    .then(({data}) => {
      this.articleFavList = data;
    })
    .catch(() => {
      alert("즐겨찾기 목록을 조회하던 중 오류가 발생했습니다")
    })
  }
};
</script>

<style scoped>
#bookmark_card {
  padding: 0 30px;
}
#card_detail {
  padding: 0px;
}
</style>
