<script>
import NavHeader from "@/views/Main/NavHeader.vue";
import NavFooter from "@/views/Main/NavFooter.vue";
import axios from 'axios'
export default {
  name: "Mainpage", 
    data() {
      return{
        items1: ['게시판', '작품', '블로그', '유저'],
        items2: ['전체선택', '알고리즘', '웹'],
        items3: ['전체선택', '알고리즘', '웹'],
        selected1: '1',
        selected2: '1',
        rows: 100,
        page: 1,
        value : "",
        book1: require("../images/book1.png"),
        book2: require("../images/book2.png"),
        book3: require("../images/book3.png"),
        book4: require("../images/book4.png"),
        book5: require("../images/book5.png"),
        book6: require("../images/book6.png"),
        book7: require("../images/book7.png"),
        book8: require("../images/book8.png"),
        book9: require("../images/book9.png"),
        bestboards : [],
        bestuserImage : [],
        pagelength : [],
        boards : [],
        userImage : [],
        pageboard : 1,
        boardlength : 1,
        booklength : 1,
        totalVisible: 5,
        bestbooks: [],
        bestbookfrontcover: [],
        bestbookmiddlecover: [],
        bestbookendcover: [],
        books: [],
        bookfrontcover: [],
        bookmiddlecover: [],
        bookendcover: [],
        bestbooksInfo: [],
        booksInfo: [],
        pagebook : 1,
        bestbookuserImage: [],
        bookuserImage: []
      }
    },
    created() {
      this.getboards()
      this.getbooks()
    },
  components : {
    NavHeader,
    NavFooter
  },
  computed: {
    userInfo() {
      return this.$store.getters.userInfo;
    },
    serverURL(){
      return this.$store.getters.serverURL;
    }
  },
  methods : {
    searchdata(){
        if(this.serach == "" || !this.search){
          alert("검색어 또는 분류를 입력하세요.");
          return;
        }
        if(this.value==="게시판"){
          this.$router.push('/mainboard/'+ this.search + '/1');
        }else if(this.value==="작품"){
          this.$router.push('/mainbook/'+ this.search + '/1');
        }else if(this.value==="블로그"){
          this.$router.push('/mainblog/'+ this.search + '/1');
        }else if(this.value==="유저"){
          this.$router.push('/mainuser/'+ this.search + '/1');
        }
      },
    toArticle(data){
      this.$router.push('/bloghome/' + data.userId + '/articledetail/' + data.articleNo)
    },
    toBook(data,datauser){
      // alert(data);
      // alert(datauser);
      this.$router.push('/bloghome/' + datauser.userId + '/bookdetail/' + data.bookNo)
    },
    getboards(){
        axios.get('/article/articles')
            .then((Response)=>{
              console.log(Response);
              console.log("드러옴");
              if(Response.data.length>2){
                for(let i = 0; i < 3; i++){
                  this.bestboards.splice(i,1,Response.data[i].article);
                  this.bestuserImage.splice(i,1,this.serverURL + Response.data[i].article.profileImg);
                }
              }
              else{
                for(let i = 0; i < Response.data.length; i++){
                  this.bestboards.splice(i,1,Response.data[i].article);
                  this.bestuserImage.splice(i,1,this.serverURL + Response.data[i].article.profileImg);
                }
              }
          }),

        axios.get('/article/articlescount')
          .then((Res)=>{
          this.boardlength = parseInt(Math.ceil(Res.data/4))
        }),

        axios.get('/article/articles?limitStart=' + ((this.pageboard-1)*4) + '&limitEnd=4')
        .then((Response)=>{
          console.log(Response);
          console.log("드러옴");
          for(let i = 0; i < Response.data.length; i++){
              this.boards.splice(i,1,Response.data[i].article);
              this.userImage.splice(i,1,this.serverURL + Response.data[i].article.profileImg);
          }
        })
      },


      getbooks(){
        axios.get('/book/books')
        .then((Response)=>{
          this.bestbooks = [];
          this.bestbookfrontcover = [];
          this.bestbookmiddlecover = [];
          this.bestbookendcover = [];
          this.bestbooksInfo = [];
          this.bestbookuserImage = [];

          if(Response.data.length>8){
            // alert(Response.data.length)
                for(let i = 0; i < 9; i++){
                  this.bestbooks.splice(i,1,Response.data[i].book)
                  this.bestbookfrontcover.splice(i,1,this.serverURL + Response.data[i].book.coverFront);
                  this.bestbookmiddlecover.splice(i,1,this.serverURL + Response.data[i].book.coverMiddle);
                  this.bestbookendcover.splice(i,1,this.serverURL + Response.data[i].book.coverBack);
                  axios.get(`user/listone/${Response.data[i].book.writer}`)
                  .then((Res)=>{
                      this.bestbooksInfo.splice(i,1,Res.data)
                      this.bestbookuserImage.splice(i,1,this.serverURL + Response.data[i].book.profileImg);
                  })
                }
            }else{
              for(let i = 0; i < Response.data.length; i++){
                      this.bestbooks.splice(i,1,Response.data[i].book)
                      this.bestbookfrontcover.splice(i,1,this.serverURL + Response.data[i].book.coverFront);
                      this.bestbookmiddlecover.splice(i,1,this.serverURL + Response.data[i].book.coverMiddle);
                      this.bestbookendcover.splice(i,1,this.serverURL + Response.data[i].book.coverBack);
                      axios.get(`user/listone/${Response.data[i].book.writer}`)
                      .then((Res)=>{
                      this.bestbooksInfo.splice(i,1,Res.data)
                      this.bestbookuserImage.splice(i,1,this.serverURL + Response.data[i].book.profileImg);
                      })
                }
            }
        }),
        axios.get('/book/books?limitStart=' + ((this.pagebook-1)*4) + '&limitEnd=4')
        .then((Response)=>{
          this.books = [];
          this.bookfrontcover = [];
          this.bookmiddlecover = [];
          this.bookendcover = [];
          this.booksInfo = [];
          this.bookuserImage = [];
          for(let i=0;i<Response.data.length;i++){
            this.books.splice(i,0,Response.data[i].book)
            this.bookfrontcover.splice(i,0,this.serverURL + Response.data[i].book.coverFront);
            this.bookmiddlecover.splice(i,0,this.serverURL + Response.data[i].book.coverMiddle);
            this.bookendcover.splice(i,0,this.serverURL + Response.data[i].book.coverBack);
            axios.get(`user/listone/${Response.data[i].book.writer}`)
            .then((Res)=>{
            this.booksInfo.splice(i,0,Res.data)
            // this.bookuserImage.splice(i,0,this.serverURL + Response.data[i].book.profileImg);
              this.bookuserImage.splice(i,0,this.serverURL + Res.data.profileImg);
            })
          }
        }),
        axios.get('/book/bookscount')
          .then((Res)=>{
          this.booklength = parseInt(Math.ceil(Res.data/4))
        })
      },
      clickboardpage() {
        this.getboards();
      },
      clickbookpage(){
        this.getbooks();
      },
      removeIframe(){
        setTimeout(() => {
          if(document.getElementsByTagName('iframe')){
            let iframes = document.getElementsByTagName('iframe');
            let len = document.getElementsByTagName('iframe').length;
            for(let i = 0; i < len; i++){
              console.log(i);
              iframes[0].parentElement.removeChild(iframes[0]);
            } 
          }else{
            alert('dd')
            setTimeout(this.removeIframe, 100);
          }
        }, 100)
      },
      removeCode(){
        setTimeout(() => {
          if(document.getElementsByTagName('code')){
            let codes = document.getElementsByTagName('code');
            let len = document.getElementsByTagName('code').length;
            for(let i = 0; i < len; i++){
              console.log(i);
              codes[0].parentElement.parentElement.removeChild(codes[0].parentElement);
            } 
          }else{
            alert('dd')
            setTimeout(this.removeCode, 100);
          }
        }, 100)
      }
  },
  updated(){
      window.Prism.highlightAll();
      this.removeIframe();
      this.removeCode();
    }
};
</script>

