            댓글
          </span>
          <!-- 코드 가능 에디터로 바꿔야함!!!! -->
          <v-text-field
            placeholder="댓글 작성"
            v-model="commentArea"
            outlined
            clearable
            dense
            required
            @input="$v.name.$touch()"
            @blur="$v.name.$touch()"
          ></v-text-field>
          <v-btn class="mx-4 font-weight-black" @click="commentWrite">작성</v-btn>
        </v-row>
      </form>

      <!-- 사용자의 댓글 -->
