</template>

<script>
import axios from 'axios';
import { required } from 'vuelidate/lib/validators'
export default {
  name: "BookDetail",
  validations: {
    name: { required },
  },
  methods: {
    backList(){
      this.$router.go(-1);
    },
    clickFav(){
      if(this.userInfo == null){
        alert("로그인이 필요한 서비스입니다.");
        this.loginpage();
        return;
      }
      axios.post(`/book/bookfavorite/CUD`,{
        bookNo : this.booktagcat.book.bookNo
      })
      .then((Response)=>{
        if(Response.data == 'insert'){
          alert("즐겨찾기 등록");
          this.booktagcat.book.favoriteDate = true;
        }
        else if(Response.data == 'delete'){
          alert("즐겨찾기 해제");
          this.booktagcat.book.favoriteDate = false;
        }
      })
      .catch(()=>{
        alert("즐겨찾기 등록/해제 도중 오류가 발생했습니다.");
      })
    },
    hitBookLike(){
      if(this.userInfo == null){
        alert("로그인이 필요한 서비스입니다.");
        this.loginpage();
        return;
      }
      axios.post(`/book/booklike/CUD`,{
        bookNo : this.booktagcat.book.bookNo
      })
      .then(()=>{
        if(this.booktagcat.book.likeDate){
          this.booktagcat.book.likeDate = false;
          this.booktagcat.book.likeCount--;
        } else{
          this.booktagcat.book.likeDate = true;
          this.booktagcat.book.likeCount++;
        }
      })
      .catch(()=>{
        alert("좋아요 등록/해제 중 오류 발생");
      })
    },
    follow() {
      axios.post(`/user/follow/`+ this.booktagcat.book.writer).then((Response)=>{
        console.log(Response);
        alert("팔로우가 되었습니다.");
        this.$router.go();
      }).catch(()=>{
        alert("에러");
      })
    },
    unfollow(){
      axios.delete(`/user/follow/`+ this.booktagcat.book.writer).then((Response)=>{
        console.log(Response);
        alert("팔로우가 삭제되었습니다.");
        this.$router.go();
      })
    },
    modifyBook(){
      this.$router.push(`../modifybook/${this.$route.params.bookNo}`)
    },
    loginpage(){
      this.$router.push(`/login`);
    },
    commentWrite(){
      if(this.commentArea.trim() == ''){
        alert('내용을 입력해주세요')
        return;
      }
      if(this.userInfo == null){
        alert("로그인 해주세요");
        this.loginpage();
        return;
      }
      axios.post('/comment/CUD',{
        bookNo : this.booktagcat.book.bookNo,
        content : this.commentArea
      })
      .then( () => {
        this.limitStart = 0;
        this.getCommentList();
        this.commentArea = '';
        alert('등록완료');
      })
      .catch( () => {
        alert('댓글 등록에 실패했습니다!')
      })
    },
    commentDelete(commentNo){
      axios.delete(`/comment/CUD?commentNo=${commentNo}`)
      .then( () => {
        this.limitStart = 0;
        this.getCommentList();
        alert("삭제 완료");
      })
      .catch( () => {
        alert("댓글을 삭제하던 중 오류가 발생했습니다");
      })
    },
    commentModify(){
      axios.put('/comment/CUD',{
        commentNo : this.editCommentNo,
        content : this.editCommentContent
      })
      .then( () => {
        this.getCommentList();
        alert('수정완료');
        this.dialog = false;
      })
      .catch( () => {
        alert('댓글 수정에 실패했습니다!')
      })
    },
    getCommentList(){
      axios.get(`comment/comments?bookNo=${this.$route.params.bookNo}`)
      .then((Response)=>{
        console.log("댓글 조회");
        console.log(Response.data);
        this.comment = Response.data;
      })
      .catch(()=>{
        alert("댓글 정보를 불러오는 데 실패했습니다.");
      })
    },
    hitCommentLike(comment){
      if(this.userInfo == null){
        alert('로그인 해주세요!')
        this.loginpage();
        return;
      }
      axios.post(`/comment/commentlike/CUD`,{
        commentNo : comment.commentNo
      })
      .then(()=>{
        //빨간색 변신

        if (comment.likeDate){
          comment.likeDate = false;
          comment.likeCount--;
        }else{
          comment.likeDate = true;
          comment.likeCount++;
        }
      })
      .catch(() =>{
        alert('좋아요 등록 중 오류 발생')
      })
    },
    pickComment(comment){
      axios.put('/comment/pick',{
        commentNo : comment.commentNo
      })
      .then(()=>{
        this.getCommentList();
      })
      .catch(() => {
        alert('댓글 채택 도중 오류가 발생했습니다')
      })
    },
    defaultbook(){
      axios.get(`/book/books?userId=${this.$route.params.userId}`)
      .then((Response) => {
          for(let i=0;i<Response.data.length;i++){
            this.blogbooks.splice(i,1,Response.data[i].book)
          }
      })
      .catch(() => {
          alert("게시판 책 목록을 가져오는 데 실패했습니다.");
      }) 
    },
    go_book(data){
      this.$router.push('/bloghome/' + this.$route.params.userId + '/bookdetail/' + data.bookNo)
    }

  },
  data(){
    return {
      booktagcat: {},
      booktag: [],
      tmptag: [],
      comment: [],
      likeCount: '',

      check: false,
      following: {},
      dialog: false,
      editCommentContent: '',
      editCommentNo: 0,

      commentArea: '',
      blogbooks: [],
    }
  },
  created(){

    let requestUrl;
    if(this.userInfo){
      requestUrl = `/book/book/${this.$route.params.bookNo}?userNo=${this.userInfo.userNo}`;
    }else{
      requestUrl = `/book/book/${this.$route.params.bookNo}`;
    }

    axios.get(requestUrl)
    .then((Response) => {
      console.log("Book 조회");
      console.log(Response.data);
      console.log(Response.data.writer);
      this.booktagcat = Response.data;

      // axios.post(`/user/list`,{
      //   searchKey : "nickName",
      //   searchValue : Response.data.nickName
      // }).then((Response)=>{
      //   console.log("저자 조회");
      //   console.log(Response.data);
      //   this.writerInfo = Response.data[0];
      // })

      axios.get('/user/following')
      .then((Response)=>{
        console.log(Response);
        this.following = Response.data;
        for(let i=0;i<Response.data.length;i++){
          if(this.$route.params.userId == Response.data[i].userId){
            this.check = true;
            break;
          }
        }
      })
      .catch(()=>{
        alert("팔로우 정보를 가져오는데 실패했습니다.");
      })
      
    })
    .catch(()=>{
      alert("책 정보를 불러오는 데 실패했습니다.");
    })

    this.getCommentList();
    this.defaultbook();

    // axios.get(`tag/booktags?bookNo=${this.$route.params.bookNo}`)
    // .then((Response)=>{
    //   console.log("책태그 조회");
    //   console.log(Response.data);
    //   this.booktag = Response.data;
    // })
    // .catch(()=>{
    //   alert("책 태그 정보를 불러오는 데 실패했습니다.");
    // })
    
    // axios.get(`tag/tmptags?bookNo=${this.$route.params.bookNo}`)
    // .then((Response)=>{
    //   console.log("임시태그 조회");
    //   console.log(Response.data);
    //   this.tmptag = Response.data;
    // })
    // .catch(()=>{
    //   alert("임시 태그 정보를 불러오는 데 실패했습니다.");
    // })

    // axios.get(`book/booklikescount?bookNo=${this.$route.params.bookNo}`)
    // .then((Response)=>{
    //   console.log("책 좋아요 갯수 조회");
    //   this.likeCount = Response.data;
    // })
    // .catch(()=>{
    //   alert("책 좋아요 정보를 불러오는데 실패했습니다.");
    // }),
  },
  updated(){
    var pages = document.getElementsByClassName('page');
    console.log('dddddddddddddddd')
    console.log(pages.length);

    for(let i = 0; i < pages.length; i++) {
      var page = pages[i];
      if (i % 2 === 0){
        page.style.zIndex = (pages.length - i);
      }
    }

    console.log('event======================================asdasdasdasds')
    
      for(let i = 0; i < pages.length; i++){
        //Or var page = pages[i];
        pages[i].pageNum = i + 1;
        pages[i].onclick=function(){
          
          if (this.pageNum % 2 === 0){
            this.classList.remove('flipped');
            this.previousElementSibling.classList.remove('flipped');
          } 
          else{
            this.classList.add('flipped');
            this.nextElementSibling.classList.add('flipped');
          }
        }
      }
      window.Prism.highlightAll();
  },
  computed : {
    serverURL(){
      return this.$store.getters.serverURL;
    },
    userInfo() {
      return this.$store.getters.userInfo;
    },
  },
}
</script>

<style>
.book {
  transition: opacity 0.4s 0.2s;
}
.page {
  width: 30vw;
  height: 44vw;
  background-color: #111111;
  float: left;
  margin-bottom: 0.5em;
  background: left top no-repeat;
  background-size: cover;
}
.page:nth-child(even) {
  clear: both;
}
.book {
  perspective: 250vw;
}
.book .pages {
  width: 80vw;
  height: 45vw;
  position: relative;
  transform-style: preserve-3d;
  backface-visibility: hidden;
  border-radius: 4px;
  /*box-shadow: 0 0 0 1px #e3dfd8;*/
}
.book .page {
  float: none;
  clear: none;
  margin: 0;
  position: absolute;
  top: 0;
  width: 40vw;
  height: 45vw;
  transform-origin: 0 0;
  transition: transform 1.4s;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  cursor: pointer;
  user-select: none;
  background-color: #f0f0f0;
}
.book .page:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0);
  transition: background 0.7s;
  /* z-index: 2; */
}
.book .page:nth-child(odd) {
  pointer-events: all;
  transform: rotateY(0deg);
  right: 0;
  border-radius: 0 4px 4px 0;
  background-image: linear-gradient(to right, rgba(0,0,0,.15) 0%, rgba(0,0,0,0) 10%);
}
.book .page:nth-child(odd):hover {
  transform: rotateY(-15deg);
}
.book .page:nth-child(odd):hover:before {
  background: rgba(0, 0, 0, 0.03);
}
.book .page:nth-child(odd):before {
  background: rgba(0, 0, 0, 0);
}
.book .page:nth-child(even) {
  pointer-events: none;
  transform: rotateY(180deg);
  transform-origin: 100% 0;
  left: 0;
  border-radius: 4px 0 0 4px;
  border-color: black;
  background-image: linear-gradient(to left, rgba(0,0,0,.12) 0%, rgba(0,0,0,0) 10%);
}
.book .page:nth-child(even):before {
  background: rgba(0, 0, 0, 0.2);
}
.book .page.grabbing {
  transition: none;
}
.book .page.flipped:nth-child(odd) {
  pointer-events: none;
  transform: rotateY(-180deg);
}
.book .page.flipped:nth-child(odd):before {
  background: rgba(0, 0, 0, 0.2);
}
.book .page.flipped:nth-child(even) {
  pointer-events: all;
  transform: rotateY(0deg);
}
.book .page.flipped:nth-child(even):hover {
  transform: rotateY(15deg);
}
.book .page.flipped:nth-child(even):hover:before {
  background: rgba(0, 0, 0, 0.03);
}
.book .page.flipped:nth-child(even):before {
  background: rgba(0, 0, 0, 0);
}
*,
* :before,
*:after {
  box-sizing: border-box;
}
.page:nth-child(odd){
  background-position: right top;
}
</style>